

.font{
  font-family: "antui-icon" !important;
  font-size: 50px;
}

.flex{
  display: flex;
  flex-wrap: wrap;
}

.box{
  width: 80px;
  margin-top: 20px;
  text-align: center;
  .name{
    font-size: 10px;
  }
  .copy{
    margin-top: 10px;
    font-size: 16px;
  }
  &:hover{
    color: #fff;
    background: #1890ff;
    border-radius: 10px;
    cursor: pointer;
    .font{
      transform: scale(1.1);
    }
  }
}

